import React from 'react';
import { Link } from 'react-router-dom';

import AuthUserContext from './AuthUserContext';
import SignOutButton from './SignOut';
import * as routes from '../constants/routes';
import './App.css';

//displays different navigation bar based on whether a user is signed in
const Navigation = () =>
    <AuthUserContext.Consumer>
        {authUser => authUser
            ? <NavAuth />
            : <NavUnauth /> 
        }
    </AuthUserContext.Consumer>

const NavAuth = () =>
    <ul className='navBar'>
        <li className='routes'><Link to={routes.HOME}>Home</Link></li>
        <li className='routes'><Link to={routes.PROJECTS}>Projects</Link></li>
        <li className='routes'><Link to={routes.PROFILES}>Profiles</Link></li>
        <li className='routes'><Link to={routes.ACCOUNT}>Account</Link></li>
        <li className='signOut'><SignOutButton /></li>
    </ul>

const NavUnauth = () =>
    <ul className='navBar'>
        <li className='routes'><Link to={routes.SIGN_IN}>Sign In</Link></li>
        <li className='routes'><Link to={routes.HOME}>Home</Link></li>
        <li className='routes'><Link to={routes.PROJECTS}>Projects</Link></li>
        <li className='routes'><Link to={routes.PROFILES}>Profiles</Link></li>
        <li className='signOut'><SignOutButton /></li>
    </ul>

export default Navigation;


